.footer {

  &__content {
    width: 100%;
  }

  &__brand {
    width: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 40px;

    @include onTablet {
      flex-direction: column;
      margin-bottom: 0;
      align-items: flex-start;
    }
  }

  &__social-media {
    display: flex;
    justify-content: end;
  }

  &__up-arrow {
    display: flex;
    justify-content: end;
    margin-bottom: 20px;

    @include onTablet {
      display: none;
    }
  }

  &__description {
    display: flex;
    box-sizing: content-box;

    margin-bottom: 40px;
    border-bottom: 1px solid $dark-green-color;

    @include onDesktop {
      margin-bottom: 70px;
    }

    @include onDesktop {
      padding-bottom: 0;
      border-bottom: none;
      margin-bottom: 90px;
    }
  }

  &__line {
    display: none;

    @include onDesktop {
      display: block;
      height: 260px;
      width: 1px;
      background-color: $dark-green-color;
      margin: 0 auto;
    }
  }

  &__authors {
    font-family: IBM Plex Sans, Roboto, sans-serif;
    font-weight: 300;
    font-size: 12px;
    color: $font-color__semi-black;

    display: flex;
    flex-direction: column;
    align-items: center;

    margin-bottom: 35px;

    @include onTablet {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__authors-description {
    margin-bottom: 10px;
  }
}
